$font-stack: Arial, Helvetica, sans-serif;
$docs-font: Didot, serif;
$background-color: yellow;
$button-color: #ffc107;
$text-color: black;
$link-text: DodgerBlue;
$table-color: #ffff66;

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  background-color: $background-color;
  text-align: center;
  font-family: $font-stack;
  line-height: 1.6;
}

a {
  text-decoration: none;
  color: $link-text;
}

a:hover {
  text-decoration: none;
  color: $text-color;
}

%btn {
  display: inline-block;
  margin: 15px;
  color: $text-color;
  font-size: 18px;
  padding: 9px 15px;
  border-radius: 20px;
  background-color: $button-color;
  border: 3px;
  border-style: outset;

  //Button Shadow Effect
  &:hover {
    transform: scale(.98);
    box-shadow: 0 12px 16px 0 rgba(0,0,0,0.24), 0 17px 50px 0 rgba(0,0,0,0.19);
  }
}

.button{
  @extend %btn;
}


.button-small{
  @extend %btn;
  font-size: 14px;
  padding: 5px 8px;
  border-radius: 15px;
  margin: 5px;
}

#uniquie_button{
  @extend %btn;
  background-color: green;
  border-radius: 50%;
}

.button-nav{
  @extend %btn;
  font-size: 14px;
  padding: 5px;
  border-radius: 10px;
  margin: 0;
}

.button-group{
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.docs{
   text-align: left;
   font-family: $docs-font;
}

.error-text{
  padding: 20px 0 0 0;
  font-size: 2vw;
}

.form-group{
  margin-left: auto;
  margin-right: auto;
  text-align: center;
  width: 50%;
}

.results{
  border: 5px outset $button-color;
}

.table{
  background-color: $table-color;
}

nav {
  display: flex;
  align-items: right;
  justify-content: space-between;

  ul {
    display: flex;
    list-style-type: none;
  }

  li {
    padding: 15px;
  }
}



@media(max-width: 600px) {
  .docs {
      img {
        display: none;
      }
    }

  .button{
    font-size: 14px;
    padding: 5px 8px;
    border-radius: 15px;
    margin: 5px;
  }
  }

@media print {
  .no-print {
    display: none;
    }
  .results{
    width: 100%;
  }
}
